@charset "UTF-8";
@font-face {
  font-family: "Abolition";
  src: url("../../fonts/FontsFree-Net-Abolition-W00-Regular.ttf") format("truetype");
}
html {
  overflow-x: hidden;
}

a, a:hover {
  color: #204E9D;
}

body {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #7E7E7E;
}
@media (min-width: 1200px) {
  body .container,
body .container-lg,
body .container-md,
body .container-sm,
body .container-xl {
    max-width: 1230px;
  }
}
body a,
body button,
body input,
body select,
body textarea {
  transition: all 0.35s;
}
body a:focus, body a:hover,
body button:focus,
body button:hover,
body input:focus,
body input:hover,
body select:focus,
body select:hover,
body textarea:focus,
body textarea:hover {
  box-shadow: none !important;
  outline: none !important;
  text-decoration: none;
  transition: all 0.35s;
}

.hidden {
  display: none;
}

.slick-dotted.slick-slider {
  margin-bottom: 0;
}
.slick-dotted.slick-slider .slick-dots {
  bottom: 15px;
}

.top-nav {
  font-family: "Montserrat", sans-serif;
  margin: auto;
  max-width: 1300px;
}
.top-nav.sticky {
  min-height: 80px;
  max-width: 100%;
  background-color: #fff;
}
.top-nav.sticky .sticky_container {
  position: fixed;
  width: 100%;
  z-index: 99999;
  background-color: #fff;
  margin: auto;
}
.top-nav.sticky .sticky_container nav.navbar {
  width: 1300px;
  margin: auto;
}
.top-nav.sticky .sticky_container nav.navbar .nav-item .dropdown-menu {
  display: none;
}
.top-nav.sticky .sticky_container nav.navbar .nav-item:hover .dropdown-menu {
  display: block;
}
.top-nav .show.dropdown .dropdown-toggle::after {
  -webkit-transition: all 350ms ease;
  transition: all 350ms ease;
  transform: scale(1);
}
.top-nav .show.dropdown .dropdown-toggle::after {
  transform: scale(-1);
}
.top-nav .dropdown-toggle::after {
  vertical-align: middle;
}
.top-nav nav.navbar {
  padding: 0;
  background-color: #FFF;
}
.top-nav nav.navbar .container-fluid {
  padding-left: 0px;
  padding-right: 0px;
}
@media only screen and (max-width: 1280px) {
  .top-nav nav.navbar .container-fluid {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.top-nav nav.navbar .navbar-brand {
  padding: 0;
  width: 220px;
  margin-right: 0;
}
.top-nav nav.navbar .nav-item.dropdown, .top-nav nav.navbar .nav-item.single-link {
  padding: 0px 12.5px;
}
.top-nav nav.navbar .nav-item .dropdown-menu {
  opacity: 0;
  margin-top: -4px;
  border-radius: 0;
  border: 0;
  background-color: transparent;
  -webkit-transition: all 350ms ease;
  transition: all 350ms ease;
  visibility: hidden;
}
.top-nav nav.navbar .nav-item .dropdown-menu a.dropdown-item:hover {
  color: #204E9D;
}
@media screen and (max-width: 1199px) {
  .top-nav nav.navbar .nav-item .dropdown-menu {
    padding: 0px;
  }
}
.top-nav nav.navbar .nav-item .dropdown-menu .dropdown-content {
  background-color: #FFF;
  padding: 8px 15px;
}
.top-nav nav.navbar .nav-item .dropdown-menu .dropdown-item {
  padding: 8px 0px;
  background: transparent;
  font-size: 14px;
}
.top-nav nav.navbar .nav-item .dropdown-menu .dropdown-item:not(:last-child) {
  border-bottom: 1px solid #bac9fa;
}
.top-nav nav.navbar .nav-item .dropdown-menu .dropdown-item.active {
  color: #204E9D !important;
}
@media screen and (max-width: 1199px) {
  .top-nav nav.navbar .nav-item .dropdown-menu .dropdown-item {
    padding: 0px;
  }
}
@media screen and (min-width: 1200px) {
  .top-nav nav.navbar .nav-item .dropdown-menu {
    display: block;
    z-index: 0;
  }
  .top-nav nav.navbar .nav-item:hover .dropdown-menu {
    display: block;
    z-index: 10;
    opacity: 1;
    visibility: visible;
  }
}
@media screen and (max-width: 1199px) {
  .top-nav nav.navbar .nav-item .dropdown-menu {
    opacity: 1;
    z-index: 1;
    visibility: visible;
  }
  .top-nav nav.navbar .nav-item .dropdown-menu .dropdown-item:not(:last-child) {
    border-bottom: 0px;
  }
  .top-nav nav.navbar .nav-item .dropdown-menu .dropdown-item.active {
    color: #204E9D !important;
  }
}
.top-nav nav.navbar .nav-item .dropdown-toggle {
  display: inline-block;
  padding: 0px 2px;
  margin-top: 3px;
}
.top-nav nav.navbar .nav-item a.nav-link {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
  display: inline-block;
  text-transform: uppercase;
  color: #000;
  font-size: 14px;
  padding: 7px 0px;
  -webkit-transition: all 350ms ease;
  transition: all 350ms ease;
}
.top-nav nav.navbar .nav-item a.nav-link:hover {
  color: #204E9D;
}
.top-nav nav.navbar .nav-item a.nav-link.active {
  color: #204E9D;
  border-bottom: 2px solid #204E9D;
}
.top-nav nav.navbar .nav-item a.nav-link.featured-link {
  margin-left: 35px;
  padding-left: 26px;
  padding-right: 26px;
  background-color: #204E9D;
  color: #FFF;
  border: 1px solid #204E9D;
}
.top-nav nav.navbar .nav-item a.nav-link.featured-link:hover {
  color: #204E9D;
  border: 1px solid #204E9D;
  background-color: #FFF;
}
@media only screen and (max-width: 1200px) {
  .top-nav nav.navbar .nav-item a.nav-link.featured-link {
    margin: 10px 10px 10px 15px;
    padding: 7px 15.5px;
    display: inline-block;
  }
}
@media only screen and (max-width: 1200px) {
  .top-nav .navbar-toggler {
    width: 56px;
    height: 50px;
    padding-top: 8px;
    padding-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s;
  }
  .top-nav .navbar-toggler .fa-times {
    line-height: 0.5;
    width: max-content;
    height: max-content;
  }
  .top-nav .navbar-toggler .fa-times:before {
    content: "×";
  }
  .top-nav .navbar-toggler .close_navbar {
    font-weight: 500;
  }
}

@media only screen and (max-width: 1199.98px) {
  .top-nav.sticky .sticky_container {
    width: 100%;
    margin-left: 0px;
    padding-left: 0px;
  }

  .top-nav.sticky .sticky_container nav.navbar {
    width: 100%;
  }
}
@media only screen and (max-width: 767.98px) {
  .top-nav nav.navbar .nav-item a.nav-link.featured-link:hover {
    background-color: #204e9d;
    color: #fff;
    border: 1px solid #204e9d;
  }
}
